<template>
    <div class="search">
        <div class="page">
            <header>
                <a href="javascript:history.back(-1)">
                    <i class="el-icon-arrow-left"></i>
                </a>
                <span>搜索</span>
            </header>

            <form class="search_form">
                <el-input
                        type="search"
                        placeholder="请输入商家或美食"
                        v-model="inputV">
                </el-input>

                <el-button type="primary" class="search_submit" @click="huntFor">提交</el-button>
            </form>

            <section v-if="isShow1">
                <h4 class="title_restaurant">商家</h4>
                <ul class="list_container">
                    <li class="list_li" v-for="p in search" @click="toShop(p)">
                        <section class="item_left">
                            <img  :src="'https://elm.cangdu.org/img/'+p.image_path" class="restaurant_img">
                        </section>
                        <section class="item_right">
                            <div class="item_right_text">
                                <p>
                                    <span class="item_right_text_s1">{{p.name}}</span>
                                    <svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="24" height="14" class="pay_icon">
                                        <polygon points="0,14 4,0 24,0 20,14" style="fill: none; stroke: rgb(255, 96, 0); stroke-width: 1;"></polygon>
                                        <line x1="1.5" y1="12" x2="20" y2="12" style="stroke: rgb(255, 96, 0); stroke-width: 1.5;">
                                        </line>
                                        <text x="3.5" y="9" style="fill: rgb(255, 96, 0); font-size: 9px; font-weight: bold;">支付</text>
                                    </svg>
                                </p>
                                <p>月售 {{p.recent_order_num}} 单</p>
                                <p>{{p.float_minimum_order_amount}} 元起送 / 距离{{p.distance}}公里</p>
                            </div>
                            <ul class="item_right_detail"></ul>
                        </section>
                    </li>
                </ul>
            </section>

            <section class="search_history" v-if="isShow2">
                <h4 class="title_restaurant">搜索历史</h4>
                <ul>
                    <li class="history_list" v-for="(s,index) in historyV">
                        <span class="history_text ellipsis"  @click="history(s,index)">{{s}}</span>
                        <span class="delete_icon" @click="clearli(s,index)">
                        <svg xmlns="http://www.w3.org/2000/svg" version="1.1">
                            <line x1="8" y1="8" x2="18" y2="18" style="stroke: rgb(153, 153, 153); stroke-width: 3;"></line>                             <line data-v-ef5a3166="" x1="18" y1="8" x2="8" y2="18" style="stroke: rgb(153, 153, 153); stroke-width: 3;"></line>
                        </svg>
                        </span>
                    </li>
                </ul>
                <div class="clear_history" @click="clearHistory">清空搜索历史</div>
            </section>

            <footer>
                <router-link to="/msite"><div ><i class="el-icon-eleme"></i><p>外卖</p></div></router-link>
                <router-link to=""><div  class="f1"><i class="el-icon-search"></i> <p>搜索</p></div></router-link>
                <router-link to=""><div><i class="el-icon-tickets"></i><p>订单</p></div></router-link>
                <router-link to="/profile"><div><i class="el-icon-s-custom"></i><p>我的</p></div></router-link>
            </footer>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return {
                isShow1:false,
                isShow2:false,
                inputV:"",
                search:[],
                msite:"",
                geohash:"",
                historyV:[],
            }
        },
        created(){
            this.msite = JSON.parse(localStorage.getItem("msite"));
            this.geohash = this.msite.geohash
            if(localStorage.getItem("history")){
                this.historyV = JSON.parse(localStorage.getItem("history"));
                this.isShow2 = true;
                this.isShow1 = false;
            }
        },
        methods:{
            huntFor(){
                if (this.inputV != ""){
                    this.axios.get("/v4/restaurants",{
                        params:{
                            geohash:this.geohash,
                            keyword:this.inputV,
                        }
                    }).then(res=>{
                        if(res == "") {
                            this.search = [{name:"很抱歉！无搜索结果"}];
                        }else {
                            this.search = res;
                            console.log(this.search);
                            this.isShow1 = true;
                            this.isShow2 = false;
                            this.historyV.push(this.inputV);
                            localStorage.setItem("history",JSON.stringify(this.historyV));
                        }
                    }).catch(err=>{});
                }

                // if(localStorage.getItem('history')==null){
                //     localStorage.setItem("history",JSON.stringify(this.inputV));
                // }

            },
            history(s,index){
              this.inputV = this.historyV[index];
                if (this.inputV != ""){
                    this.axios.get("/v4/restaurants",{
                        params:{
                            geohash:this.geohash,
                            keyword:this.inputV,
                        }
                    }).then(res=>{
                        if(res == "") {
                            this.search = [{name:"很抱歉！无搜索结果"}];
                        }else {
                            this.search = res;
                            console.log(this.search);
                        }
                    }).catch(err=>{});
                };
                this.isShow1 = true;
                this.isShow2 = false;
            },
            clearli(s,index){
                this.historyV.splice(index,1);
            },
            clearHistory(){
                localStorage.removeItem("history");
                this.isShow2 = false;
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            }
        }
    }
</script>

<style scoped>
    .search{
        width: 100%;
        overflow: hidden;
    }
    .page{
        padding-top: 1.95rem;
    }
    .page p,.page span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .search_form {
        background-color: #fff;
        padding: .5rem;
        display: flex;
    }
    .search_form el-input {
        height: 1.5rem;
    }
    .search_form .search_submit {
        flex: 1;
        margin-left: .2rem;
        font-size: .65rem;
        font-weight: 700;
    }

    .title_restaurant {
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .list_container {
        background-color: #fff;
    }
    .list_li {
        display: flex;
        padding: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .list_li .item_left {
        margin-right: .25rem;
    }
    .list_li .item_left .restaurant_img {
        width: 1.7rem;
        height: 1.7rem;
    }
    .list_li .item_right {
        font-size: .55rem;
        flex: 1;
    }
    .list_li .item_right .item_right_text {
        padding-bottom: .25rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .list_li .item_right .item_right_detail {
        margin-top: .25rem;
    }
    .list_li .item_right .item_right_text p {
        line-height: .9rem;
    }
    .list_li .item_right .item_right_text .pay_icon {
        margin-bottom: -.08rem;
    }
    .item_right_text_s1{
        margin-right: .2rem;
    }

    .search_history .history_list {
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        font: .7rem/2rem Microsoft YaHei;
        padding: 0 .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .search_history .history_list .history_text {
        display: inline-block;
        width: 80%;
    }
    .ellipsis {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    .search_history .history_list .delete_icon {
        width: 1rem;
        height: 1rem;
    }
    .search_history .clear_history {
        background-color: #fff;
        color: #3190e8;
        font: .7rem/2rem Microsoft YaHei;
        font-weight: 700;
        text-align: center;
    }

    footer{
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 1.95rem;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    footer div{
        width: 25%;
        height: 1.95rem;
        text-align: center;
        align-items: center;
        float: left;
    }

    footer i{
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        margin-right: .2rem;
        color: #666;
    }
    .f1 i{
        color:#3190e8;
    }
    footer p{
        font-size: .45rem;
        color: #666;
    }
</style>